<template>
    <div>
        <div class="page-header">
            <ul class="nav nav-pills">
                <li role="presentation">
                    <a href="#/feature/list">数据</a>
                </li>
                <li role="presentation" class="active">
                    <a href="#/feature/create_record">项目列表</a>
                </li>
            </ul>
        </div>

        <div class="row pad-top-10">
            <table class="table">
                <thead>
                    <tr>
                        <th>提交日期</th>
                        <th>项目名称</th>
                        <th>状态</th>
                        <th>创建人</th>
                        <th>详情</th>
                    </tr>
                </thead>
                <tbody>
                    <template v-for="item in list">
                        <tr>
                            <td>
                                <strong>{{item.submit_time}}</strong>
                            </td>
                            <td>
                                <a href="javascript:;" @click="showDetail(item)">{{item.name}}</a>
                            </td>
                            <td>{{item.status_text}}</td>
                            <td>{{item.creator}}</td>
                            <td>{{item.describe}}</td>
                        </tr>
                    </template>
                </tbody>
            </table>
        </div>

        <modal modalId="recordDetailId" modalSize="modal-md">
            <template slot="header">
                <h4 class="modal-title" id="myModalLabel">详情</h4>
            </template>
            <ul class="list-group">
                <li class="list-group-item">
                    <strong>项目名称：{{detail.name}}</strong>
                </li>
                <li class="list-group-item">
                    <strong>数据源类型：HADOOP</strong>
                </li>
                <li class="list-group-item">
                    <strong>数据源目录：</strong>
                    {{detail.source_dir}}
                </li>
                <li class="list-group-item">
                    <strong>存储类型：</strong>
                    {{detail.db_type}}
                </li>
                <li class="list-group-item">
                    <strong>存储前缀：</strong>
                    {{detail.source_prefix}}
                </li>
                <li class="list-group-item">
                    <strong>源数据格式：</strong>
                    {{srcDataFormatText}}
                </li>
                <li class="list-group-item">
                    <strong>保存版本数量：</strong>
                    {{detail.save_version_count}}
                </li>
                <li class="list-group-item">
                    <strong>数据量大小：</strong>
                    {{detail.data_size}}
                </li>
                <li class="list-group-item">
                    <strong>最大数据行数：</strong>
                    {{detail.max_data_line}}
                </li>
                <li class="list-group-item">
                    <strong>检查周期：</strong>
                    {{detail.check_cycle}}
                </li>
                <li class="list-group-item">
                    <strong>重新加载周期：</strong>
                    {{detail.reload_cycle}}
                </li>
                <li class="list-group-item">
                    <strong>数据存活时间：</strong>
                    {{detail.expire_time}}
                </li>
            </ul>
            <template slot="footer">
              <!--<button type="button" class="btn btn-default" data-dismiss="modal" @click="modify()">修改-->
              <!--</button>-->
            </template>
        </modal>

    </div>
</template>

<script>
export default {
    name:'FeatureCreateRecord',
    data() {
        return {
            list: [],
            detail: {
                data_format_json: {
                    data_format: "",
                    storage_format: "",
                    delimiter: ""
                }
            }
        };
    },
    mounted() {
        this.getList();
    },
    computed: {
        srcDataFormatText: function() {
            return `数据结构：${
                this.$config.common.dataStructConf[
                    this.detail.data_format_json.data_format
                ]
            }，数据存储格式：${
                this.$config.common.dataTypeConf[
                    this.detail.data_format_json.storage_format
                ]
            }，分隔符：${this.detail.data_format_json.delimiter}`;
        }
    },
    methods: {
        async getList() {
            let res = await this.$api.feature.getSubmitHistory();

            if (res.errcode != 0) {
                this.$set(this, "list", []);
                return;
            }

            this.$set(this, "list", res.data);
        },
        showDetail(detail) {
            this.detail = detail;
            this.detail.data_format_json = JSON.parse(this.detail.data_format);
            $("#recordDetailId").modal("show");
            localStorage.setItem('tempDetail', JSON.stringify(detail));
        },
        modify(){
            // $("#recordDetailId").modal("none");
          this.$router.push({
            path: `/feature/update/${this.detail.id}`,
          })
        }
    }
};
</script>
