<script>
  export default {
    name:'FeatureUpdateRecord',
    data() {
      return {
        updateParam: {
          name: "",
          source_type : "hadoop",
          source: "",
          prefix : "",
          source_dir: "",
          db_type: "redis",
          source_prefix: "",
          src_data_format: "",
          data_size: "",
          max_data_line: 0,
          check_cycle: "",
          reload_cycle: "",
          expire_time: "",
          mail_list: "",
          b_mailing: 1
        },
        srcDataStruct: "key-value",
        srcDataFormat: {
          storage_format: "string",
          delimiter: ","
        }
      };
    },
    mounted() {
      this.getDetail()
    },
    computed: {
      srcDataFormatText: function() {
        return `数据结构：${
          this.$config.common.dataStructConf[this.srcDataStruct]
          }，数据存储格式：${
          this.$config.common.dataTypeConf[this.srcDataFormat.storage_format]
          }，分隔符：${this.srcDataFormat.delimiter}`;
      }
    },
    watch: {
      srcDataStruct(val, oldVal) {
        // if (val == "key-value") {
        //   this.srcDataFormat.storage_format = "string";
        // } else {
        //   this.srcDataFormat.storage_format = "int_binary";
        // }
      }
    },
    methods: {
      getDetail(){
        var res = JSON.parse(localStorage.getItem('tempDetail'));
        this.updateParam = res ;
        this.srcDataStruct = res.data_format_json.data_format;
        this.srcDataFormat = res.data_format_json;
        // let res =
        // this.createParam = res.
      },
      goback(){
        this.$router.go(-1);
      },

      async update() {
        //console.log(this.createParam);
        //return;
        //this.createParam.max_data_line = parseInt(this.createParam.max_data_line);
        let updateParam = this.updateParam;
        updateParam.data_format = JSON.stringify({
          data_format : this.srcDataStruct,
          storage_format : this.srcDataFormat.storage_format,
          delimiter : this.srcDataFormat.delimiter,
        });
        updateParam.creator = localStorage.getItem('userName');
        let res = await this.$api.feature.create(this.updateParam);

        if (res.errcode != 0) {
          alert("修改失败");
          return;
        }
        alert("修改成功");
      },
      async verifyStoragePrefix() {
        if (this.updateParam.prefix == "") {
          this.$bootbox.alert("请输入存储前缀");
          return;
        }
        let res = await this.$api.feature.verifyStoragePrefix(
          this.updateParam.prefix
        );

        if (res.errcode != 0) {
          this.$bootbox.alert("请求出错");
          return;
        }

        if (res.data.status == "true") {
          this.$bootbox.alert("验证成功");
        } else {
          this.$bootbox.alert("验证失败");
        }
      },
      showSrcDataFormatModal(modalShow) {
        $("#srcDataFormatId").modal(modalShow);
      }
    }
  };
</script>
