import Vue from 'vue'
import Router from 'vue-router'

import Index from './Sea'
import FeatureLakeList from './Feature/List'
import FeatureCreate from './Feature/Create'
import FeatureCreateRecord from './Feature/CreateRecord'
import FeatureUpdateRecord from './Feature/Update'


Vue.use(Router)

const router = new Router({
  //mode : 'history',
  routes: [
    {
        path : '/',
        name : 'Index',
        component: Index
    },
    {
        path : '/feature/list',
        name : 'FeatureLakeList',
        component: FeatureLakeList
    },
    {
        path : '/feature/create',
        name : 'FeatureCreate',
        component: FeatureCreate
    },
    {
        path : '/feature/create_record',
        name : 'FeatureCreateRecord',
        component: FeatureCreateRecord

    },
    {
      path : '/feature/update/:id',
      name : 'FeatureUpdateRecord',
      component: FeatureUpdateRecord
    }
  ],
})

router.afterEach((to,from,next) => {
   window.scrollTo(0,0);
});
export default router;
