<template>

<div id="app">

    <myheader />

    <div class="main-content">

        <div class="center-block" :class="{'view' : $route.path != '/'}">
            <router-view></router-view>
        </div>
    </div>


</div>

</template>
<style scoped>
@media screen and (min-width: 1001px) {
    .content-area {
        margin-left: 16.8%;
        margin-top: 70px;
    }
}

@media screen and (max-width: 1001px) {
    .content-area {
      margin-left: 1%;
    }
}
.view {
    padding:60px 30px;
    width: 80%
}
</style>
<script>
//components
import myheader from './header'

//css style
import '@/assets/css/bootstrap-3.3.7.css'
import '@/assets/css/common.less'

import lodash from 'lodash';
import moment from 'moment';

export default {
    name: 'Home',
    data() {
        return {
          showSlideMenu: 1,
          currentMenuData : [],
          userType : 0,
          userName : '',
          PageInitAt : '',
        }
    },
    components : {
        'myheader' : myheader,
    },
    methods: {
        async getUserInfo() {

            let res = await this.$api.feature.getUserInfo();

            if(res.errcode == 0 && Object.keys(res.data).length > 0) {
                localStorage.setItem('userName', res.data.user_name);
            } else {
                window.location.href = '/';
            }
            // if(this.userType <= 0 ) {
            //   this.$bootbox.alert('你暂时没有访问沧海的权限');
            //   setTimeout(function() {
            //     window.location.href = '/';
            //   }, 2000);
            // }
            // return;

        },
        handleChildMounted() {
            this.checkUpdatePage();
        },
        checkUpdatePage : function() {

            let initPageAt = this.PageInitAt;

            if(moment(initPageAt).isBefore(moment().subtract(3600, 's'))) {
                window.location.reload();
            }
        },
        unFixedTableHead: function() {
            $(window).unbind('scroll');
        },
        clickToSelect : function(name) {
            this.toExpandSlideMenu();
        },
        setPageInitTime : function() {
            this.PageInitAt = moment().format('YYYY-MM-DD HH:mm:ss');
        }

    },
    mounted() {
        this.getUserInfo();
        this.setPageInitTime();
    },
    watch:{
        $route (to,from){
          const fname = from.name;
          const tname = to.name;
          if(from.meta.isRefresh || ( fname == 'FeatureUpdateRecord' && tname == 'CreateRecord')){
            from.meta.isRefresh='false';
          }
        }
    }
}
</script>
