                    >
                </div>
            </div>

            <div class="form-group">
                <label for="dataSourceType" class="col-sm-2 control-label">存储类型:</label>
                <div class="col-sm-6">
                    <label class="radio-inline">
                        <input
                            type="radio"
                            name="dataSourceType"
                            value="redis"
                            v-model="createParam.db_type"
                        >REDIS
                    </label>
                    <label class="radio-inline">
                        <input
                            type="radio"
