        <modal modalId="srcDataFormatId" modalSize="modal-lg">
            <template slot="header">
                <h4 class="modal-title" id="myModalLabel">源数据格式</h4>
            </template>
            <div class="form-horizontal">
                <div class="form-group">
                    <label for="project" class="col-sm-2 control-label">数据结构:</label>
                    <div class="col-sm-6">
                        <label class="radio-inline">
                            <input
                                type="radio"
                                name="data_struct"
                                value="key-value"
                                v-model="srcDataStruct"
                            >kv
                        </label>
                        <label class="radio-inline">
                            <input
                                type="radio"
                                name="data_struct"
                                value="vector"
                                v-model="srcDataStruct"
                            >vector
                        </label>
                    </div>
                </div>
                <div class="form-group">
                    <label for="project" class="col-sm-2 control-label">数据存储格式:</label>
                    <div class="col-sm-6">
                        <label class="radio-inline" v-if="srcDataStruct == 'key-value'">
                            <input
                                type="radio"
                                name="data_type"
                                value="string"
                                v-model="srcDataFormat.storage_format"
                            >string
                        </label>
                        <label class="radio-inline" v-if="srcDataStruct == 'vector'">
                            <input
                                type="radio"
                                name="data_type"
                                value="int_binary"
                                v-model="srcDataFormat.storage_format"
                            >int二进制数据
                        </label>
                        <label class="radio-inline" v-if="srcDataStruct == 'vector'">
                            <input
                                type="radio"
                                name="data_type"
                                value="float32_binary"
                                v-model="srcDataFormat.storage_format"
                            >float64二进制数组
                        </label>
                    </div>
