                    class="btn btn-default btn-danger"
                    @click="showSrcDataFormatModal('hide')"
                >确定</button>
            </template>
        </modal>
    </div>
</template>

<script>
export default {
    data() {
        return {
            createParam: {
                name: "",
                source_type : "hadoop",
                source: "",
                prefix : "",
                source_dir: "",
                db_type: "redis",
                source_prefix: "",
                src_data_format: "",
                data_size: "",
                max_data_line: 0,
                check_cycle: "",
                reload_cycle: "",
                expire_time: "",
                mail_list: "",
                b_mailing: 1
            },
            srcDataStruct: "key-value",
            srcDataFormat: {
                storage_format: "string",
                delimiter: ","
            }
        };
    },
    computed: {
        srcDataFormatText: function() {
            return `数据结构：${
                this.$config.common.dataStructConf[this.srcDataStruct]
            }，数据存储格式：${
                this.$config.common.dataTypeConf[this.srcDataFormat.storage_format]
            }，分隔符：${this.srcDataFormat.delimiter}`;
        }
    },
    watch: {
        srcDataStruct(val, oldVal) {
            if (val == "key-value") {
                this.srcDataFormat.storage_format = "string";
            } else {
                this.srcDataFormat.storage_format = "int_binary";
            }
        }
    },
    methods: {
        async create() {
            //console.log(this.createParam);
            //return;
            //this.createParam.max_data_line = parseInt(this.createParam.max_data_line);
            let createParam = this.createParam;
            createParam.data_format = JSON.stringify({
                data_format : this.srcDataStruct,
                storage_format : this.srcDataFormat.storage_format,
                delimiter : this.srcDataFormat.delimiter,
            });
            createParam.creator = localStorage.getItem('userName');
            let res = await this.$api.feature.create(this.createParam);

            if (res.errcode != 0) {
                alert("提交失败");
                return;
            }
            alert("提交成功");
        },
        async verifyStoragePrefix() {
            if (this.createParam.prefix == "") {
                this.$bootbox.alert("请输入存储前缀");
                return;
            }
            let res = await this.$api.feature.verifyStoragePrefix(
                this.createParam.prefix
            );

            if (res.errcode != 0) {
                this.$bootbox.alert("请求出错");
                return;
            }

            if (res.data.status == "true") {
                this.$bootbox.alert("验证成功");
            } else {
                this.$bootbox.alert("验证失败");
            }
        },
        showSrcDataFormatModal(modalShow) {
            $("#srcDataFormatId").modal(modalShow);
        }
    }
};
</script>
