<template>
<div>

    <div class="page-header">
        <ul class="nav nav-pills">
          <li role="presentation" class="active">
                    <a href="#">数据</a>
                </li>
                <li role="presentation">
                    <a href="#/feature/create_record">项目列表</a>
                </li>
        </ul>
    </div>

    <h4>数据加载列表</h4>
    <div class="row">
        <div class="col-md-3">
            <div class="form-group">
              <label for="project">项目：</label>
              <select class="form-control" v-model="project">
                  <template v-for="(item, p) in projectList">
                      <option  :value="item">{{item}}</option>
                  </template>
              </select>
            </div>
        </div>
        <div class="col-md-3">
          <div class="form-group">
            <label for="dataSource">数据源：</label>
                <select class="form-control" v-model="dataSource">
                    <option  :value="''">所有</option>
                    <template v-for="(item, p) in sourceList">
                        <option  :value="item">{{item}}</option>
                    </template>
                </select>
          </div>
        </div>
        <div class="col-md-3">
          <div class="form-group">
            <label for="version">版本：</label>
            <select class="form-control" v-model="version">
                <option  :value="''">所有</option>
                <template v-for="(item, p) in versionList">
                    <option  :value="item">{{item}}</option>
                </template>
            </select>
          </div>
        </div>
        <div class="col-md-3">
            <div class="form-group">
                <label for="version"></label>
                <div>
                    <button type="submit" class="btn btn-primary" @click="searchList">查  询</button>
                </div>
            </div>
        </div>
    </div>

    <div class="row pad-top-10">
        <table class="table">
            <thead>
                <tr>
                    <th>更新时间</th>
                    <th>数据源</th>
                    <th>版本</th>
                    <th>进度</th>
                    <th>状态</th>
                </tr>
            </thead>
            <tbody>
                <template v-for="item in list">
                    <tr>
                        <td><strong>{{item.update_time}}</strong></td>
                        <td>{{item.source}}</td>
                        <td>{{item.version}}</td>
                        <td>{{item.process}}%</td>
                        <td>{{item.status}}</td>
                    </tr>
                </template>
            </tbody>
        </table>
    </div>


</div>
</template>

<script>

export default {
    name:'List',
    data(){
        return {
            project : "",
            version : '',
            dataSource : '',
            projectList : [],
            sourceList : [],
            versionList : [],
            list : [],
        }
    },
    watch : {
        project : function() {
            this.getSourceList();
        },
        dataSource : function() {
            this.getVersionList();
        },
        version : function() {
            this.searchList();
        }
    },
    mounted() {
        this.getProjectList();
    },
    methods:{
        async getProjectList() {
            let res = await this.$api.feature.getProjectList();
            this.projectList = res.data;
            if(this.projectList != null &&this.projectList.length > 0) {
                this.project = this.projectList[0];
            }

        },
        async getSourceList() {
            let res = await this.$api.feature.getSourceList(this.project);
            this.sourceList = res.data;
            this.getVersionList();
        },
        async getVersionList() {
            let res = await this.$api.feature.getVersionList(this.project, this.dataSource);
            this.versionList = res.data;
            this.searchList();
        },
        async searchList() {

            let res = await this.$api.feature.getFeatureList(this.project, this.dataSource, this.version);

            if(res.errcode != 0) {
                this.$set(this, 'list', []);
                return;
            }

            this.$set(this, 'list', res.data);

        }
    }
}
</script>
